button {
    display: block;
    background: none;
    border: none;
    padding: 0;
    font-family: inherit;
    font-size: inherit;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    outline: none;
    width: 100%;
    height: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button:active {
    -webkit-box-shadow: inset 0 0 80px 0 rgba(0, 0, 0, 0.25);
            box-shadow: inset 0 0 80px 0 rgba(0, 0, 0, 0.25);
}



